<template>
  <div>
    <div
      class="modal fade"
      id="registration-login-form-popup"
      tabindex="-1"
      role="dialog"
      aria-labelledby="registration-login-form-popup"
      aria-hidden="true"
    >
      <div class="modal-dialog window-popup registration-login-form-popup" role="document">
        <div class="modal-content">
          <a href="#" class="close icon-close" data-dismiss="modal" aria-label="Close">
            <svg class="olymp-close-icon">
              <use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon" />
            </svg>
          </a>
          <div class="modal-body">
            <div class="registration-login-form">
              <!-- Nav tabs -->
              <ul class="nav nav-tabs" role="tablist">
                <li class="nav-item">
                  <a class="nav-link " id="hm1" data-toggle="tab" href="#home1" role="tab">
                    <svg class="olymp-login-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-login-icon" />
                    </svg>
                  </a>
                </li>
                <li class="nav-item">
                  <a class="nav-link active" id="pf1" data-toggle="tab" href="#profile1" role="tab">
                    <svg class="olymp-register-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-register-icon" />
                    </svg>
                  </a>
                </li>
              </ul>

              <!-- Tab panes -->
              <div class="tab-content">
                <div class="tab-pane" id="home1" role="tabpanel" data-mh="log-tab">
                  <div class="title h6">
                    <div
                      class="img-wrap"
                      style="width:150px;float:left;margin-right:20px;"
                    >
                      <a href="#">
                        <img
                          style="max-width: 80px;"
                          src="/image/logos/logo_blue.png"
                          alt="Match Day Deals"
                        />
                      </a>
                    </div>
                    <h4 style="margin-top:6px;">Create Account</h4>
                  </div>
                  <div
                    v-if="withEmail === false"
                    class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12"
                    style="margin-top:20px;"
                  >
                    <button
                      type="button"
                      class="btn btn-lg bg-google full-width btn-icon-left"
                    >
                      <i class="fab fa-google" aria-hidden="true"></i>Continue with Google
                    </button>
                    <button
                      type="button"
                      @click="authenticate('facebook')"
                      class="btn btn-lg bg-facebook full-width btn-icon-left"
                    >
                      <i class="fab fa-facebook-f" aria-hidden="true"></i>Continue with Facebook
                    </button>
                    <button
                      type="button"
                      @click="authenticate('twitter')"
                      class="btn btn-lg bg-twitter full-width btn-icon-left"
                    >
                      <i class="fab fa-twitter" aria-hidden="true"></i>Continue with Twitter
                    </button>

                    <div class="or"></div>

                    <a
                      href="#"
                      @click="() => { withEmail = true }"
                      class="btn btn-lg bg-breez full-width btn-icon-left"
                    >
                      <i class="fab fa-envelope" aria-hidden="true"></i>Continue with Email
                    </a>

                    <p style="margin-top:25px;">
                      By proceeding, you agree to our
                      <a href="#">Terms of Use</a> and confirm you have read our
                      <a href="#">Privacy Policy</a>.
                    </p>
                    <p style="margin-top:25px;">
                      Already have an account?
                      <a
                        data-toggle="tab"
                        href="#profile1"
                        role="tab"
                      >Click Here</a> to Login Now!
                    </p>
                  </div>
                  <form v-if="withEmail === true" class="content">
                    <div class="row">
                      <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                        <response-message :data="errorData" />
                      </div>
                      <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                        <span style="width: 100%" class="lform" v-if="formErrors.username !== ''">
                          <div class="form-group label-floating has-error">
                            <label class="control-label">Username</label>
                            <input
                              class="form-control form-control-danger"
                              placeholder
                              value
                              v-model="formModelRegister.username"
                              type="text"
                              autocomplete="username"
                            />
                          </div>
                        </span>
                        <div
                          v-else-if="formErrors.username === ''"
                          class="form-group label-floating"
                        >
                          <label class="control-label">Username</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            v-model="formModelRegister.username"
                            type="text"
                            autocomplete="username"
                          />
                        </div>
                        <span style="width: 100%" class="lform" v-if="formErrors.email !== ''">
                          <div class="form-group label-floating has-error">
                            <label class="control-label">Your Email</label>
                            <input
                              class="form-control form-control-danger"
                              placeholder
                              value
                              v-model="formModelRegister.email"
                              type="email"
                              autocomplete="Email"
                            />
                          </div>
                        </span>
                        <div v-else-if="formErrors.email === ''" class="form-group label-floating">
                          <label class="control-label">Your Email</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            v-model="formModelRegister.email"
                            type="email"
                            autocomplete="Email"
                          />
                        </div>

                        <div class="form-group label-floating">
                          <label class="control-label">Your Password</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            v-model="formModelRegister.password"
                            type="password"
                            autocomplete="password"
                          />
                        </div>
                        <div class="form-group label-floating">
                          <label class="control-label">Confirm Password</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            v-model="formModelRegister.password_confirmation"
                            type="password"
                            autocomplete="confirm_password"
                          />
                        </div>
                        {{ response }}
                        <div class="col col-lg-8 col-md-8 col-sm-12 col-12 mb30">
                          <h6>Register as a Business</h6>
                          <p>By setting to business you agree to our business policy stated in our Terms</p>
                          <div class="tg-list">
                            <li class="tg-list-item">
                              <input
                                class="tgl tgl-ios"
                                id="business"
                                value="0"
                                v-model="formModelRegister.business_reg"
                                type="checkbox"
                              />
                              <label class="tgl-btn" for="business"></label>
                            </li>
                          </div>
                        </div>

                        <!-- <div class="form-group label-floating is-select">
                                                <label class="control-label">Your Gender</label>
                                                <select class="selectpicker form-control">
                                                    <option value="MA">Male</option>
                                                    <option value="FE">Female</option>
                                                </select>
                        </div>-->

                        <div class="remember">
                          <label class="checkBoxCont">
                            I accept the
                            <a href="#">Terms and Conditions</a> of the website
                            <input
                              type="checkbox"
                              value="0"
                              v-model="formModelRegister.terms"
                            />
                            <span class="checkmark"></span>
                          </label>
                          <p>
                            Want to use Facebook or Google Instead?
                            <a
                              href="javascript:;"
                              @click="() => { withEmail = false }"
                            >Social Login</a>
                          </p>
                        </div>

                        <button
                          class="btn btn-lg bg-mdd btn-lg full-width"
                          id="registerButton"
                          type="submit"
                          @click.prevent="RegisterAccount"
                        >Complete Registration!</button>
                      </div>
                    </div>
                  </form>
                </div>

                <div class="tab-pane active" id="profile1" role="tabpanel" data-mh="log-tab">
                  <div class="title h6">
                    <div
                      class="img-wrap"
                      style="width:150px;float:left;margin-right:20px;"
                    >
                      <a href="#">
                        <img
                          style="max-width: 80px;"
                          src="/image/logos/logo_blue.png"
                          alt="Match Day Deals"
                        />
                      </a>
                    </div>
                    <h4 style="margin-top:6px;">Sign In</h4>
                  </div>
                  <div
                    v-if="withEmailLogin === false"
                    class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12"
                    style="margin-top:20px;"
                  >
                    <a href="#" class="btn btn-lg bg-google full-width btn-icon-left">
                      <i class="fab fa-google" aria-hidden="true"></i>Continue with Google
                    </a>
                    <a href="#" class="btn btn-lg bg-facebook full-width btn-icon-left">
                      <i class="fab fa-facebook-f" aria-hidden="true"></i>Continue with Facebook
                    </a>
                    <a href="#" class="btn btn-lg bg-twitter full-width btn-icon-left">
                      <i class="fab fa-twitter" aria-hidden="true"></i>Continue with Twitter
                    </a>

                    <div class="or"></div>

                    <a
                      href="#"
                      @click="() => { withEmailLogin = true }"
                      class="btn btn-lg bg-breez full-width btn-icon-left"
                    >
                      <i class="fab fa-envelope" aria-hidden="true"></i>Continue with Email
                    </a>

                    <p style="margin-top:25px;">
                      By proceeding, you agree to our
                      <a href="#">Terms of Use</a> and confirm you have read our
                      <a href="#">Privacy Policy</a>.
                    </p>
                    <p style="margin-top:25px;">
                      Don't have have an account?
                      <a
                        data-toggle="tab"
                        href="#home1"
                        role="tab"
                      >Click Here</a> to Create an account.
                    </p>
                  </div>
                  <form
                    v-if="withEmailLogin === true"
                    class="content"
                    id="login-form"
                    method="post"
                  >
                    <div class="row">
                      <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                        <response-message :data="errorData" />
                      </div>
                      <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                        <div class="form-group label-floating">
                          <label class="control-label">Your Email</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            id="email"
                            v-model="formModelLogin.email"
                            type="email"
                            autocomplete="email"
                          />
                        </div>
                        <div class="form-group label-floating">
                          <label class="control-label">Your Password</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            id="password"
                            v-model="formModelLogin.password"
                            type="password"
                            autocomplete="password"
                          />
                        </div>

                        <div class="remember">
                          <label class="checkBoxCont">
                            Remember Me
                            <input type="checkbox" value="0"
                                   v-model="formModelLogin.remember" />
                            <span class="checkmark"></span>
                          </label>
                          <p>
                            Want to use Facebook or Google Instead?
                            <a
                              href="javascript:;"
                              @click="() => { withEmailLogin = false }"
                            >Social Login</a>
                          </p>
                          <a
                            data-toggle="tab" href="#forgot" role="tab"
                            class="forgot"
                          >Forgot my Password</a>
                        </div>
                        <button
                          class="btn btn-lg bg-mdd full-width"
                          id="loginButton"
                          type="submit"
                          @click.prevent="LoginAccount"
                        >Login</button>

                        <!--<div class="or"></div>

                                            <a href="#" class="btn btn-lg bg-facebook full-width btn-icon-left"><i class="fab fa-facebook-f" aria-hidden="true"></i>Login with Facebook</a>

                        <a href="#" class="btn btn-lg bg-twitter full-width btn-icon-left"><i class="fab fa-twitter" aria-hidden="true"></i>Login with Twitter</a>-->

                        <p>
                          Don’t you have an account?
                          <a
                            data-toggle="tab"
                            href="#home1"
                            role="tab"
                          >Register Now!</a> it’s really simple and you can start enjoing all the benefits!
                        </p>
                      </div>
                    </div>
                  </form>
                </div>
                <div class="tab-pane" id="forgot" role="tabpanel" data-mh="log-tab">
                  <div class="title h6">
                    <div
                      class="img-wrap"
                      style="width:150px;float:left;margin-right:20px;"
                    >
                      <a href="#">
                        <img
                          style="max-width: 80px;"
                          src="/image/logos/logo_blue.png"
                          alt="Match Day Deals"
                        />
                      </a>
                    </div>
                    <h4 style="margin-top:6px;">Reset Your Password</h4>
                  </div>
                  <form
                    class="content"
                    id="forgot-form"
                    method="post"
                  >
                    <div class="row">
                      <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                        <response-message :data="errorData" />
                      </div>
                      <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                        <div class="form-group label-floating is-empty">
                          <label class="control-label">Your Email</label>
                          <input
                            class="form-control"
                            placeholder
                            value
                            id="forgot_email"
                            v-model="formModelForgot.email"
                            type="email"
                            autocomplete="email"
                          />
                        </div>

                        <div class="remember">
                          <a
                            class="forgot"
                            data-toggle="tab" href="#profile1" role="tab"
                          >Want to Login</a>
                        </div>
                        <button
                          class="btn btn-lg bg-mdd full-width"
                          id="resetButton"
                          type="submit"
                          @click.prevent="ResetAccount"
                        >Reset Password</button>

                        <!--<div class="or"></div>

                                            <a href="#" class="btn btn-lg bg-facebook full-width btn-icon-left"><i class="fab fa-facebook-f" aria-hidden="true"></i>Login with Facebook</a>

                        <a href="#" class="btn btn-lg bg-twitter full-width btn-icon-left"><i class="fab fa-twitter" aria-hidden="true"></i>Login with Twitter</a>-->

                        <p>
                          Don’t you have an account?
                          <a
                            data-toggle="tab"
                            href="#home1"
                            role="tab"
                          >Register Now!</a> it’s really simple and you can start enjoing all the benefits!
                        </p>
                      </div>
                    </div>
                  </form>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ls from "../localstorage";
import jquery from "jquery";
var pageData = {
  projectName: "",
  formErrors: {
    username: "",
    email: "",
    password: "",
    confirm_password: ""
  },
  formModelLogin: {
    email: "",
    password: "",
    remember: false,
  },
  formModelForgot: {
    email: "",
  },
  formModelRegister: {
    email: "",
    password: "",
    password_confirmation: "",
    username: "",
    business_reg: false,
    terms: false
  },
  userData: {},
  errorData: {},
  withEmail: true,
  withEmailLogin: true,
  isAuthenticated: '',
  access_token: null,
  response: null
};
var $ = jquery;
var pageMethods = {
  ResetAccount() {
    if (!this.$validate("email",this.formModelLogin.email)) {
      this.errorData = {
        message: "Sorry please check your email",
        isError: true
      };
    }else {
      $("#resetButton").html("Processing...");
      this.errorData = { isError: false }; // hide error box
      const requestPayload = {
        email: this.formModelForgot.email,
      };
      this.axios.post('/password/reset/create', requestPayload)
        .then(response => {
          $("#resetButton").html("Reset Password");
          if (response.data.status === false) {
            this.errorData = {
              message: response.data.message,
              isError: true
            };
          }else {
            this.errorData = {
              message: "We have sent you a mail to reset your password",
              isError: "success"
            };
          }
        })
        .catch(error => {
          $("#resetButton").html("Reset Password");
          this.errorData = {
            message: error.response
                    ? "Please check your Login Details"
                    : "Sorry an error occurred, check your internet",
            isError: true
          };
        })
    }
  },
  LoginAccount() {
    // here we first check the login input
    if (this.formModelLogin.email === "") {
      this.errorData = {
        message: "Sorry an email is required",
        isError: true
      };
    } else if (this.formModelLogin.password === "") {
      this.errorData = {
        message: "Sorry password is required",
        isError: true
      };
    } else {
      $("#loginButton").html("Processing...");
      this.errorData = { isError: false }; // hide error box
      // // console.log(JSON.stringify(this.formModelLogin));

      this.axios
        .post(this.$store.state.rawURI + "user/login", {
          email: this.formModelLogin.email,
          password: this.formModelLogin.password
        })
        .then(response => {
          // store data in localstorage
          // date timestamp for 24 hours
          var ts = Math.round(new Date().getTime() / 1000);
          var tsYesterday = ts + (24 * 3600);

          // here in the token_generation
          //we check if the user has set remember me
          // if so set timestamp from server api else,
          // give them a 24hours time to use the system
          var storeTicket = {
            token: response.data.data.token,
            token_generation: this.formModelLogin.remember === undefined || this.formModelLogin.remember === "" ? tsYesterday : response.data.data.expires_at * 1000 ,
          };
          var storeFansData = {
            id: response.data.data.id,
            username: response.data.data.username,
            email: response.data.data.email,
            firstname: response.data.data.firstname,
            lastname: response.data.data.lastname,
            club: response.data.data.club,
            avatar: response.data.data.avatar,
            user_type: response.data.data.user_type
          };
          // // console.log(response.data.data);
          if (ls.clearStorage("ticket") && ls.clearStorage("fansData")) {
            ls.setStorage("ticket", storeTicket);
            ls.setStorage("fansData", storeFansData);
          }
          //reset the form
          this.resetForm();
          $("#loginButton").html("Login");
          // show the success message
          this.errorData = {
            message: "Logged in Redirecting ....",
            isError: "success"
          };
          // reload the browser
          let currentPath = window.location.pathname;
          window.location = currentPath;
        })
        .catch(error => {
          $("#loginButton").html("Login");
          this.errorData = {
            message: error.response
              ? "Please check your Login Details"
              : "Sorry an error occured, check your internet",
            isError: true
          };
        });
    }

    // // console.log('clicked login');
  },
  validateForm() {
    const shortTerm = "Complete Registration";
    if (!this.$validate("email", this.formModelRegister.email)) {
      $("#registerButton").html(shortTerm);
      this.errorData = {
        message: "Sorry please check your email again",
        isError: true
      };
      return false;
    }else if( this.formModelRegister.password.length < 5 ) {
      $("#registerButton").html(shortTerm);
      this.errorData = {
        message: "Sorry passwords must not be less than 5 characters",
        isError: true
      };
      return false;
    }else if(!this.$validate("username", this.formModelRegister.username)) {
      $("#registerButton").html(shortTerm);
      this.errorData = {
        message: "Sorry please check your username again",
        isError: true
      };
      return false;
    }else {
      return true;
    }
  },
  RegisterAccount() {
    const shortTerm = "Complete Registration";

    if (this.validateForm()) {
      if (this.formModelRegister.email === "") {
        $("#registerButton").html(shortTerm);
        this.errorData = {
          message: "Sorry an email is required",
          isError: true
        };
      } else if (this.formModelRegister.password === "") {
        $("#registerButton").html(shortTerm);
        this.errorData = {
          message: "Sorry you need a password for this account",
          isError: true
        };
      } else if (this.formModelRegister.password_confirmation === "") {
        $("#registerButton").html(shortTerm);
        this.errorData = {
          message: "Sorry we need you to confirm your password",
          isError: true
        };
      } else if (
              this.formModelRegister.password_confirmation !==
              this.formModelRegister.password
      ) {
        $("#registerButton").html(shortTerm);
        this.errorData = {
          message: "Sorry your passwords do not match",
          isError: true
        };
      } else if (this.formModelRegister.username === "") {
        $("#registerButton").html(shortTerm);
        this.errorData = {
          message: "Sorry please do choose a unique username",
          isError: true
        };
      } else if (!this.formModelRegister.terms) {
        $("#registerButton").html(shortTerm);
        this.errorData = {
          message: "Sorry you must accept our terms and conditions",
          isError: true
        };
      } else {
        $("#registerButton").html("Processing...");
        this.errorData = { isError: false }; // hide error box

        if (this.formModelRegister.business_reg) {
          this.registerApi("business");
        } else {
          this.registerApi("user");
        }
      }
    }
  },
  registerApi(form) {
    var api;
    const shortTerm = "Complete Registration";
    if (form === "business") {
      api = this.$store.state.rawURI + "business/register";
    } else {
      api = this.$store.state.rawURI + "user/register";
    }
    this.axios
      .post(api, {
        email: this.formModelRegister.email,
        password: this.formModelRegister.password,
        password_confirmation: this.formModelRegister.password_confirmation,
        username: this.formModelRegister.username
      })
      .then(response => {
        if (response.status === 200 || response.success === true) {
          //reset the form
          this.resetForm();
          $("#registerButton").html(shortTerm);
          // show the success message
          this.errorData = {
            message: "Registration Successfull, please do login",
            isError: "success"
          };
          $("#profile1").addClass("active");
          $("#home1").removeClass("active");
          $("#pf1").addClass("active");
          $("#hm1").removeClass("active");
        } else {
          this.errorData = {
            message: response.status
              ? " Sorry an error occurred while registering, please contact the administrator <a href='mailto:support@matchdaydeals.com'>support@matchdaydeals.com</a>"
              : "Sorry an error occurred, check your internet",
            isError: true
          };
        }
      })
      .catch(error => {
        $("#registerButton").html(shortTerm);
        // console.log(error.response.data.data);
        if (error.response.status === 400) {
          this.formErrors.username = error.response.data.data.username;
          this.formErrors.email = error.response.data.data.email;

          this.errorData = {
            message: error.response
              ? error.response.data.message +
                "\n" +
                error.response.data.data.username +
                "\n" +
                error.response.data.data.email
              : "Sorry an error occured, check your internet",
            isError: true
          };
        }
      });
  },
  resetForm() {
    this.formModelLogin = {
      email: "",
      password: ""
    };
    this.formModelRegister = {
      email: "",
      password: "",
      password_confirmation: "",
      username: ""
    };
  },
  authenticate(provider) {
    if (this.$auth.isAuthenticated()) {
      this.$auth.logout()
    }
    this.response = null
    var this_ = this;
    this.$auth
      .authenticate(provider)
      .then(response => {
        // Execute application logic after successful social authentication
        this_.isAuthenticated = this_.$auth.isAuthenticated();
        // console.log(response);
        return response;
      })
      .catch(err => {
        this_.isAuthenticated = this_.$auth.isAuthenticated()
        this_.response = err
        // console.log(err)
      });
      // console.log('auth instance', this.$auth);
  }
};

export default {
  name: "AuthModal",
  data() {
    return pageData;
  },
  methods: pageMethods,
  mounted() {
    this.resetForm();
    this.errorData = { isError: false }; // hide error box
    this.projectName = this.$store.state.projectName;
    // let recaptchaScript = document.createElement('script')
    // recaptchaScript.setAttribute('src', '/js/libs/daterangepicker.js')
    // document.head.appendChild(recaptchaScript)
    if (this.isAuthenticated) {
      // console.log('Authenticated',)
    }
  }
};
</script>

<style>
.lform span {
  width: 100%;
}
</style>
